
import { axios } from "../../core/axios";

const url = process.env.REACT_APP_ROOT_API;


const token = localStorage.getItem('token')

export const PackageApi = {
  async fetchPackage(id) {
    const {data} = await axios.get(`${url}api/user/inside/package/${id}`
);
    return data;
  },
}

export function equiryRequest(postData) {
  console.log(token)
  return axios.post(`${url}api/user/enquiry/addEnquiry`, postData, 
  {
  headers: {
    'Access-Control-Allow-Origin' : token,
    'Access-Control-Allow-Methods':'GET,PUT,POST,DELETE,PATCH,OPTIONS',
    }
  }
  )
    .then(res => {
      console.log('Submit Data Sucessfully')
    }).catch(res => {
      console.log('Erroe..............')
    })
}